import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import store from "../store";
Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
    meta: {
      global: true,
      title: "الرئسية | عرب بيست",
      progress: {
        func: [
          { call: "color", modifier: "temp", argument: "#ffb000" },
          { call: "fail", modifier: "temp", argument: "#6e0000" },
          { call: "location", modifier: "temp", argument: "top" },
          {
            call: "transition",
            modifier: "temp",
            argument: { speed: "1.5s", opacity: "0.6s", termination: 400 },
          },
        ],
      },
    },
  },
  {
    path: "/about",
    name: "About",
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/About.vue"),
  },
  {
    path: "/auth/login",
    name: "Login",
    component: () => import("../views/auth/login.vue"),
    meta: {
      authRequired: false,
      title: "تسجيل الدخول",
    },
  },
  {
    path: "/addpost",
    name: "Upload",
    component: () => import("../views/player/upload.vue"),
    meta: {
      authRequired: true,
      title: "إضافة  فيديو",
    },
  },
  {
    path: "/account",
    name: "Account",
    component: () => import("../views/admin/account.vue"),
    meta: {
      authRequired: true,
      title: "تحديت الإسم",
    },
  },
  {
    path: "/videos",
    name: "Videos",
    component: () => import("../views/admin/posts.vue"),
    children: [
      {
        path: ":id",
        name: "updateViedo",
        component: () => import("../views/admin/updatepost.vue"),
      },
    ],
    meta: {
      authRequired: true,
      title: "فيديوهاتي",
    },
  },
  {
    path: "/update/:id",
    name: "Update",
    component: () => import("../views/admin/updatepost.vue"),
    meta: {
      authRequired: true,
      title: "تعديل الفيديو",
    },
  },
  {
    path: "/watch",
    name: "Watch",
    component: () => import("../views/player/go.vue"),
    children: [
      {
        path: ":id",
        name: "ViewMessages",
        component: () => import("../views/player/watch.vue"),
      },
    ],
    meta: {
      global: true,
      title: "watch video",
    },
  },
  {
    path: "/trend",
    name: "Trend",
    component: () => import("../views/player/trend.vue"),
    meta: {
      global: true,
      title: "الأكتر مشاهدة",
    },
  },
  {
    path: "/iframe/:id",
    name: "Iframe",
    component: () => import("../components/iframe.vue"),
    meta: {
      authRequired: true,
      title: "watch video",
    },
  },
  {
    path: "/auth/register",
    name: "Register",
    component: () => import("../views/auth/register.vue"),
    meta: {
      authRequired: false,
      title: "إنشاء حساب",
    },
  },
  {
    path: "/not_found",
    alias: "*",
    name: "NotFound",
    component: () => import("@/views/NotFound.vue"),
    meta: {
      // global: true,
      title: "not found",
    },
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
  // scrollBehavior() {
  //   return { x: 0, y: 0 };
  // },
});
router.afterEach((to) => {
  Vue.nextTick(() => {
    document.title = to.meta.title || "arabest";
  });
});
router.beforeEach((to, from, next) => {
  // console.log(store.getters.getToken);
  if (to.matched.some((record) => record.meta.authRequired)) {
    if (store.getters.isLoggedIn) {
      next();
      return;
    } else {
      next({
        name: "Login",
        params: to.params,
        query: {
          nextPath: to.name,
        },
      });
    }
  } else if (to.matched.some((record) => record.meta.global)) {
    next();
  } else {
    if (store.getters.isLoggedIn) {
      // next();
      next({
        name: "Home",
      });
    } else {
      next();
    }
  }
});
export default router;
