import Vue from "vue";
import App from "./App.vue";
import "./registerServiceWorker";
import router from "./router";
import store from "./store";
import patterns from "./store/patterns";
import Notifications from "vue-notification";
import VueSweetalert2 from "vue-sweetalert2";
import "sweetalert2/dist/sweetalert2.min.css";
//// import plugs
import "@mdi/font/css/materialdesignicons.css";
import "./assets/css/style.css";
// import "./assets/css/videojs.css";
import "./plugins/axios.js";
import VueVideoPlayer from "vue-video-player";
import VueProgressBar from "vue-progressbar";
import VueMeta from "vue-meta";
import vuetify from "./plugins/vuetify.js";
import VueTimeago from "vue-timeago";
import vPlayBack from "v-playback";
import VueSocialSharing from "vue-social-sharing";
import io from "socket.io-client";
import VueSocketIO from "vue-socket.io";
import VueMoment from "vue-moment";
import moment from "moment-timezone";
import Ads from "vue-google-adsense";
import Adsense from "vue-google-adsense/dist/Adsense.min.js";
import InArticleAdsense from "vue-google-adsense/dist/InArticleAdsense.min.js";
import InFeedAdsense from "vue-google-adsense/dist/InFeedAdsense.min.js";

Vue.use(require("vue-script2"));
Vue.use(Ads.Adsense);
Vue.use(Ads.InArticleAdsense);
Vue.use(Ads.InFeedAdsense);
Vue.use(Adsense);
Vue.use(InArticleAdsense);
Vue.use(InFeedAdsense);

Vue.use(VueMoment, {
  moment,
});
Vue.use(
  new VueSocketIO({
    debug: true,
    // https://3erabest.com
    connection: io("wss://livebotola.com", {
      // autoConnect: false,
      extraHeaders: {
        "x-auth-token": store.getters.getToken,
      },
      // transports: ["websocket", "polling"],
    }),

    vuex: {
      store,
      actionPrefix: "SOCKET_",
      mutationPrefix: "SOCKET_",
    },
  })
);
Vue.prototype.soundEffect = new Audio(
  "https://static.xx.fbcdn.net/rsrc.php/yy/r/XFhtdTsftOC.ogg"
);
Vue.use(VueSocialSharing);
Vue.use(vPlayBack);

Vue.use(VueMeta, { refreshOnceOnNavigation: true });
Vue.use(VueTimeago, {
  name: "timeago",
  locale: "ar",
  locales: {
    en: require("date-fns/locale/en"),
    ar: require("date-fns/locale/ar"),
  },
});
const options = {
  color: "#bffaf3",
  failedColor: "#874b4b",
  thickness: "5px",
  transition: {
    speed: "0.2s",
    opacity: "0.1s",
    termination: 300,
  },
  autoRevert: false,
  location: "right",
  inverse: false,
};

Vue.use(VueProgressBar, options);

import "video.js/dist/video-js.css";
Vue.use(VueVideoPlayer);

Vue.use(Notifications);
Vue.use(VueSweetalert2);

Vue.config.productionTip = false;
Vue.prototype.$pattern = patterns;

new Vue({
  router,
  store,
  vuetify,
  render: (h) => h(App),
}).$mount("#app");
